<template>
  <Container :title="title">
    <v-form :advanced="advanced" @submit="submit">
      <template slot="base">
        <group :label="lang.pid" type="select" :options="type_options" v-model="arctype.pid"/>
        <group :label="lang.typename" type="input" v-model="arctype.typename"  />
        <group :label="lang.litpic" type="image" v-model="arctype.litpic" />
        <group :label="lang.description" type="editor" v-model="arctype.description" />
        <group type="status" v-model="arctype.status" />
        <group :label="lang.sorts" type="input"  v-model="arctype.sorts"/>
      </template>

      <template slot="advanced">
        <group :label="lang.mid" type="select" :options="model_options" v-model="arctype.model_id" />
        <group :label="lang.dirs" type="input" v-model="arctype.dirs" />
        <group :label="lang.keywords" type="input" v-model="arctype.keywords"/>
        <group :label="lang.icon" type="input" v-model="arctype.icon" />
        <group :label="$t('status.createTime')" type="input" :disabled="true" v-model="arctype.create_time" />

        <group :label="$t('status.updateTime')" type="input" :disabled="true" v-model="arctype.edit_time" />

      </template>
    </v-form>

  </Container>
</template>

<script>
  import {Container,VForm,Group} from "@/components"

  export default {
    name: "edit",
    components: {Group, VForm, Container },
    data(){
      return {
        disabled:true,
        title:this.$t('arctype.editTitle'),
        advanced:true,
        lang:this.$t('arctype'),
      }
    },
    computed:{
      arctype:{
        set(value){
          this.$store.commit('arctype/SET_ARCTYPE', value)
        },
        get(){
          return this.$store.state.arctype.arctype
        }
      },
      type_options(){
        return this.$store.state.arctype.type_options

      },
      model_options(){
        return this.$store.state.arctype.model_options
      }
    },

    methods:{
      submit(){
        this.$store.dispatch('arctype/editArctype').then( resp =>{
          if (resp)
            this.$toast.success({
              title:'API 请求成功',
              message: resp.error
            })
        })
      },
    },
    created(){
      const id = this.$route.query['id']
      this.$store.dispatch('arctype/initial', id)
    }
  }
</script>



